* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins Light';
}

html, body {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.288);
}
body {
    display: flex;
    justify-content: center;
    align-items: center;
}

#app {
    border: 1px rgba(0, 0, 0, 0.25) solid;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.35);
    display: flex;
    flex-direction: row;
    border-radius: 6px;
    height: 65vh;
    width: 60vw;
    background-color: white;
    z-index: 1;
    object-fit: contain;
    overflow-x: auto;
}

.nav-bar {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    padding: 0 25px;
    margin-right: 2vw;
    margin-top: 7.5vh;
    width: 12.5vw;
}
.nav-btn {
    border: none;
    font-size: 16px;
    text-align: left;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 1rem;
    cursor: pointer;
    background-color: white;
    padding: 5px 10px;
    transition: background-color 300ms;
}
.nav-btn:hover {
    background-color: rgba(128, 128, 128, 0.25);
    border-radius: 6px;
}
.nav-icon {
    width: 1.8rem;
    height: 1.8rem;
}
#do-list-container {
    flex-grow: 4;
    margin-top: min(8%, 55px);
    display: flex;
    flex-direction: column;
    gap: 1.4rem;
    min-width: 30vw;
}
#do-list-title {
    font-size: 1.6rem;
    font-family: 'Poppins Medium';
}
.do-list-container {
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
}
.do-list {
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
}
.do-list-subtitle {
    font-size: 1.2rem;
    font-family: 'Poppins Light';
}
.do-list-task {
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    gap: 5px;
    font-size: 1rem;
    cursor: pointer;
    background-color: white;
    padding: 5px 10px;
    transition: background-color 300ms;
    border-radius: 8px;
    max-width: 25vw;
}
.do-list-task-header {
    font-family: 'Poppins Light';
    font-size: 1rem;
}
.do-list-add-task-btn {
    border: none;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 10px;
    font-size: 1rem;
    cursor: pointer;
    background-color: white;
    padding: 5px 10px;
    transition: background-color 300ms;
    border-radius: 8px;
    max-width: 25vw;
}
.do-list-add-task-btn:hover {
    background-color: rgba(128, 128, 128, 0.25);
}
.do-list-add-task-btn:active {
    transition: background-color 300ms;
    background-color: rgba(128, 128, 128, 0.35);;
}
.do-add-btn {
    width: 1.25rem;
    height: 1.25rem;
}
#modal-root {
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
}
#modal {
    position: absolute;
    top: 35%;
    z-index: 10;
    padding: 25px 20px;
    border: 1px rgba(0, 0, 0, 0.25) solid;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.35);
    border-radius: 6px;
    background-color: white;
    flex-direction: column;
    gap: 20px;
    font-size: 16px;
    animation: fadein 0.5s;
}
.modal-form {
    width: 100%;
}
@keyframes fadein {
    from {
        opacity: 0;
    } to {
        opacity: 1;
    }
}
.modal-root {
    z-index: 5;
}
.modal {
    display: flex;
}


.modal-body {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.modal-field-container {
    display: flex;
    flex-direction: row;
    width: 100%;
}
#modal input {
    flex: auto;
    font-family: 'Poppins Medium';
    font-size: 1rem;
    padding: 2px 5px;
    border-radius: 6px;
    color: rgba(128, 128, 128, 0.9);
}
#modal input:focus {
    outline-width: 0;
}
#modal-btn-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
#modal-btn-container button {
    border: 0;
}

#task-name-input {
    appearance: none;
    border: 0;
    width: 100%;
}
#date-input {
    border: 1px rgba(0, 0, 0, 0.3) solid;
}
.modal-subtitle {
    font-size: 1rem;
    font-family: 'Poppins Medium';
    color: rgba(95, 95, 95, 0.75);
}
#priority-container {
    display: flex;
    flex-direction: row;
    gap: 1.2rem;
}
.priority-radio-container {
    display: flex;
    flex-direction: column;
    box-shadow: none;
    gap: 3px;
}
.priority-radio, .priority-radio-label {
    cursor: pointer;
}
.priority-radio-label {
    font-size: 0.9rem;
    font-family: 'Poppins Medium';
    color: rgb(85, 85, 85);
}
#modal-submit {
    font-size: 16px;
    background-color: white;
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 6px;
    transition: background-color 300ms;
    background-color: rgb(190, 127, 10);
    color: white;
    font-family: 'Poppins Medium';
    font-size: 0.9rem;
}
#modal-submit:hover {
    background-color: rgb(190, 127, 10, 0.6);
    /* background-color: rgba(145, 116, 62, 0.781); */
}
#modal-submit:active {
    transition: background-color 300ms;
    background-color: rgb(190, 127, 10);
}
.empty-field {
    border: 2px red solid !important;
